import {Project} from "./project";

/**
 * Sample of the application {@link Project} with a name.
 * @category Project
 * @public
 */
export interface ProjectSample {
    /**
     * name of the sample
     * @type string
     * @public
     */
    name: string,
    /**
     * sample project
     * @type Project
     * @public
     */
    project: Project
}

